@import '../../assets/mixins';

.list {
  @include contained;
  border-radius: 0.5rem;
  margin: 2rem auto;
  background-color: #fff;
  box-shadow: 0 1rem 4rem rgba(black, 0.3);
  position: relative;

  &-private {
    @include contained;
    position: relative;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));

    grid-gap: 2rem;
  }
}
